$(function() {
	$('.tab li').on("click", function() {
		var index = $('.tab li').index(this);
		$('.tabContent > li').css('display','none');
		$('.tabContent li').eq(index).css('display','block');
		$('.tab li').removeClass('select');
		$(this).addClass('select');
    return false;
	});

  $(".aco__btn").on("click", function() {
      $(this).next().slideToggle();
      $(this).toggleClass("active");
  });


	var $accBtn = $('.result__linkBox__more'),
      $accContents = $('.ac__content');
  $accContents.hide();
	$accBtn.each(function() {
		var flg = "close";
		$(this).on("click", function() {
			$(this).prev().slideToggle();
	    if(flg == "close"){
	      $(this).text("とじる");
	      flg = "open";
	    }else{
	      $(this).text("モットみる");
	      flg = "close";
	    }
	    var myPlatform = $(this).parent().parent().find('.block__text li:first-child').attr('class');
	    $(this).prev().find('li').addClass(myPlatform);
	  });
	});
});

var swiper = new Swiper('.swiper-container', {
  grabCursor: true,
  centeredSlides: true,
  slidesPerView: 'auto',
  loop: true,
  coverflowEffect: {
    stretch: 0,
    depth: 100,
    modifier: 1,
    slideShadows : true,
  },
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
});
